import Vue from 'vue'

import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		newsList: [
			{
				text:'',
				imageList:[],
				date:'12/12 08:32'
			}
		]
	},
	getters: {
		newsList(state) {
			return state.newsList
		}
	},
	mutations: {
		sendNews(state, news) {
			state.newsList.unshift(news)
		}
	},
	actions: {

	}
})

export default store
